.banner-text{
    background-color: #d60909;
    opacity: .7;
    width: 75%;
    margin: auto;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.banner-text h1{
    font-size: 66;
    -webkit-text-stroke: 1.5px black;
    font-weight: bold;
    color: white;
    text-align: center;
}